body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display:flex;
  justify-content: center;
  align-content: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.message {
  font-size: 1.2em;
  background-color: rgba(150, 226, 226, 0.2);
  text-align: left;
  line-height: 1.5;
}

.message:nth-child(even) {
  background-color: rgba(8, 51, 243, 0.151);

}

.message-author {
  font-weight: bold;
  margin-right: 0.5em;
}
.form {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  width: 100%;
}

.form-input-row {
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
}

.form-input {
  width: 100%;
  padding: 10px 15px;
  border: 2px solid #ddd;
  box-sizing: border-box;
}

.form-msg-send {
  padding: 0 30px;
  background-color: rgb(66, 164, 194);
  color: #fff
}

.form-msg-list {
  width: 100%;
}
.app-header {
  text-align: center;
}

.app-user-login {
  display: flex;
  justify-content: center;
  align-content: center;
  margin-bottom: 1em;
}

.btn {
  font-weight: bold;
  font-size: 1.2em;
}

.credits {
  text-align: center;
}
